// CSS Custom Properties for theming
:root {
  // ---------------------------------------------------------------
  // Colors - Light Theme (default)
  // ---------------------------------------------------------------
  --bg-primary: #ffffff;
  --bg-secondary: #f5f5f5;
  --bg-tertiary: #e0e0e0;
  --text-primary: #1a1a1a;
  --text-secondary: #666666;
  --text-tertiary: #999999;
  --accent: #0066cc;
  --accent-hover: #0052a3;
  --border: #e0e0e0;
  --border-light: #f0f0f0;
  --code-bg: #f4f4f4;
  --code-text: #1a1a1a;
  --shadow: rgba(0, 0, 0, 0.1);
  --shadow-hover: rgba(0, 0, 0, 0.15);

  // ---------------------------------------------------------------
  // Typography
  // ---------------------------------------------------------------
  --font-sans: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  --font-mono: 'JetBrains Mono', 'Fira Code', 'SF Mono', Consolas, Monaco, 'Courier New', monospace;

  --font-size-xs: 0.75rem;      // 12px
  --font-size-sm: 0.875rem;     // 14px
  --font-size-base: 1rem;       // 16px
  --font-size-md: 1.125rem;     // 18px
  --font-size-lg: 1.25rem;      // 20px
  --font-size-xl: 1.5rem;       // 24px
  --font-size-2xl: 1.875rem;    // 30px
  --font-size-3xl: 2.25rem;     // 36px
  --font-size-4xl: 3rem;        // 48px

  --line-height-tight: 1.25;
  --line-height-base: 1.6;
  --line-height-relaxed: 1.75;

  --font-weight-normal: 400;
  --font-weight-medium: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;

  // ---------------------------------------------------------------
  // Spacing
  // ---------------------------------------------------------------
  --space-xs: 0.25rem;    // 4px
  --space-sm: 0.5rem;     // 8px
  --space-md: 1rem;       // 16px
  --space-lg: 1.5rem;     // 24px
  --space-xl: 2rem;       // 32px
  --space-2xl: 3rem;      // 48px
  --space-3xl: 4rem;      // 64px
  --space-4xl: 6rem;      // 96px

  // ---------------------------------------------------------------
  // Layout
  // ---------------------------------------------------------------
  --content-width: 720px;
  --wide-width: 1280px;
  --header-height: 60px;
  --footer-height: auto;

  // TOC Sidebar
  --toc-sidebar-width: 280px;
  --toc-sidebar-gap: 3rem;

  // ---------------------------------------------------------------
  // Transitions
  // ---------------------------------------------------------------
  --transition-fast: 0.15s ease;
  --transition-base: 0.3s ease;
  --transition-slow: 0.5s ease;

  // ---------------------------------------------------------------
  // Border Radius
  // ---------------------------------------------------------------
  --radius-sm: 4px;
  --radius-md: 8px;
  --radius-lg: 12px;
  --radius-full: 9999px;

  // ---------------------------------------------------------------
  // Z-index
  // ---------------------------------------------------------------
  --z-dropdown: 1000;
  --z-sticky: 1020;
  --z-fixed: 1030;
  --z-modal-backdrop: 1040;
  --z-modal: 1050;
  --z-popover: 1060;
  --z-tooltip: 1070;
}

// ---------------------------------------------------------------
// Dark Theme
// ---------------------------------------------------------------
[data-theme="dark"] {
  --bg-primary: #1a1a1a;
  --bg-secondary: #242424;
  --bg-tertiary: #2d2d2d;
  --text-primary: #e0e0e0;
  --text-secondary: #a0a0a0;
  --text-tertiary: #707070;
  --accent: #4d9fff;
  --accent-hover: #3385ff;
  --border: #333333;
  --border-light: #2a2a2a;
  --code-bg: #2d2d2d;
  --code-text: #e0e0e0;
  --shadow: rgba(0, 0, 0, 0.3);
  --shadow-hover: rgba(0, 0, 0, 0.4);
}

// ---------------------------------------------------------------
// Responsive Breakpoints (for reference in media queries)
// ---------------------------------------------------------------
$breakpoint-xs: 480px;
$breakpoint-sm: 640px;
$breakpoint-md: 768px;
$breakpoint-lg: 1024px;
$breakpoint-xl: 1280px;
$breakpoint-2xl: 1536px;
