// Layout styles

// Main container
.main {
  max-width: var(--content-width);
  margin: 0 auto;
  padding: var(--space-xl) var(--space-md);
  min-height: calc(100vh - var(--header-height) - 200px);

  @media (max-width: $breakpoint-md) {
    padding: var(--space-lg) var(--space-md);
  }
}

// Wide container (for pages with sidebar)
.main-wide {
  max-width: var(--wide-width);
}

// Container utilities
.container {
  max-width: var(--content-width);
  margin: 0 auto;
  padding: 0 var(--space-md);
}

.container-wide {
  max-width: var(--wide-width);
  margin: 0 auto;
  padding: 0 var(--space-md);
}

// Flex utilities
.flex {
  display: flex;
}

.flex-col {
  flex-direction: column;
}

.items-center {
  align-items: center;
}

.justify-center {
  justify-content: center;
}

.justify-between {
  justify-content: space-between;
}

.gap-sm {
  gap: var(--space-sm);
}

.gap-md {
  gap: var(--space-md);
}

.gap-lg {
  gap: var(--space-lg);
}

// Grid utilities
.grid {
  display: grid;
}

.grid-cols-2 {
  grid-template-columns: repeat(2, 1fr);
}

.grid-cols-3 {
  grid-template-columns: repeat(3, 1fr);
}

// Responsive grid
@media (max-width: $breakpoint-md) {
  .grid-cols-2,
  .grid-cols-3 {
    grid-template-columns: 1fr;
  }
}

// Wide container for posts with sidebar
.main-wide {
  max-width: var(--wide-width);
}
