/* Post Single Page */
.post-single {
  max-width: var(--content-width);
  margin: 0 auto;
  padding: var(--space-2xl) var(--space-lg);
}

/* Breadcrumbs */
.breadcrumbs {
  margin-bottom: var(--space-lg);
  font-size: var(--font-size-sm);
  color: var(--text-secondary);

  ol {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: var(--space-xs);
    list-style: none;
    padding: 0;
    margin: 0;
  }

  li {
    display: flex;
    align-items: center;
    gap: var(--space-xs);
  }

  a {
    color: var(--text-secondary);
    text-decoration: none;
    display: flex;
    align-items: center;
    gap: var(--space-xs);
    transition: color var(--transition-fast);

    &:hover {
      color: var(--accent);
    }
  }

  .icon {
    width: 16px;
    height: 16px;
  }

  .separator {
    color: var(--text-tertiary);
  }

  .current {
    color: var(--text-primary);
  }
}

/* Post Cover */
.post-cover {
  margin: 0 0 var(--space-xl) 0;
  border-radius: var(--radius-md);
  overflow: hidden;

  img {
    width: 100%;
    height: auto;
    display: block;
    object-fit: cover;
  }

  figcaption {
    padding: var(--space-sm);
    text-align: center;
    font-size: var(--font-size-sm);
    color: var(--text-secondary);
    background-color: var(--bg-secondary);
  }
}

/* Post Header */
.post-header {
  margin-bottom: var(--space-xl);
}

.post-title {
  font-size: var(--font-size-3xl);
  font-weight: var(--font-weight-bold);
  line-height: var(--line-height-tight);
  color: var(--text-primary);
  margin: 0 0 var(--space-md) 0;
}

/* Post Meta */
.post-meta {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: var(--space-md);
  font-size: var(--font-size-sm);
  color: var(--text-secondary);
}

.post-meta-item {
  display: flex;
  align-items: center;
  gap: var(--space-xs);

  .icon {
    width: 16px;
    height: 16px;
    flex-shrink: 0;
  }
}

.post-categories a,
.category-link {
  color: var(--accent);
  text-decoration: none;
  transition: opacity var(--transition-fast);

  &:hover {
    opacity: 0.8;
  }
}

/* Post Layout with Sidebar */
.post-layout {
  display: grid;
  grid-template-columns: 240px 1fr;
  gap: var(--space-xl, 1rem);
  align-items: start;
}

/* Left Sidebar for TOC */
.post-sidebar {
  position: sticky;
  top: calc(var(--header-height, 60px) + var(--space-md, 1rem));
  max-height: calc(100vh - var(--header-height, 60px) - var(--space-2xl, 3rem));
  overflow-y: auto;
  align-self: start;

  /* Custom scrollbar */
  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-thumb {
    background: var(--border);
    border-radius: 3px;
  }
}

.post-main {
  min-width: 0;
  width: 100%;
}

/* TOC Container */
.toc-container {
  background-color: var(--bg-secondary);
  border-radius: var(--radius-md, 8px);
  padding: var(--space-md, 1rem);
  border: 1px solid var(--border);
  position: relative;
}

/* Inline TOC styling (when TOC is inside post-single, not in sidebar) */
.post-single > .toc-container {
  margin-bottom: var(--space-lg, 1.5rem);
  max-width: 800px;
  width: 100%;
}

.toc-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.toc-toggle {
  display: flex;
  align-items: center;
  gap: var(--space-sm, 0.5rem);
  background: none;
  border: none;
  color: var(--text-primary);
  font-size: var(--font-size-sm, 0.875rem);
  font-weight: var(--font-weight-semibold, 600);
  cursor: pointer;
  padding: 0;
  transition: color 0.2s;

  &:hover {
    color: var(--accent);
  }

  .icon {
    width: 18px;
    height: 18px;
  }
}

.toc-content {
  ol {
    list-style: none;
    padding-left: var(--space-md, 1rem);
    margin: 0;
  }

  > ol {
    padding-left: 0;
  }

  li {
    margin: var(--space-xs, 0.25rem) 0;
    position: relative;
  }

  a {
    color: var(--text-secondary);
    text-decoration: none;
    transition: all 0.2s;
    display: block;
    padding: var(--space-xs, 0.25rem) var(--space-sm, 0.5rem);
    border-radius: var(--radius-sm, 4px);
    font-size: var(--font-size-sm, 0.875rem);
    line-height: 1.5;

    &:hover {
      color: var(--accent);
      background-color: var(--bg-tertiary, var(--bg-secondary));
    }
  }

  /* Active state with left border indicator */
  .active > a {
    color: var(--accent);
    font-weight: var(--font-weight-medium, 500);
    background-color: var(--bg-tertiary, var(--bg-secondary));
    border-left: 2px solid var(--accent);
    padding-left: calc(var(--space-sm, 0.5rem) + 2px);
  }

  /* Nested indentation */
  ol ol {
    border-left: 1px solid var(--border);
    margin-left: var(--space-xs, 0.25rem);
  }
}

/* Collapsed state */
.toc-container:not(.toc-open) .toc-content {
  display: none;
}

/* Responsive breakpoints */
@media (max-width: $breakpoint-lg) {
  .post-layout {
    grid-template-columns: 220px 1fr;
    gap: var(--space-lg, 1.5rem);
  }
}

@media (max-width: $breakpoint-md) {
  .post-layout {
    display: block;
  }

  .post-sidebar {
    position: relative;
    top: auto;
    max-height: none;
    margin-bottom: var(--space-xl, 2rem);
  }

  .toc-container {
    position: sticky;
    top: calc(var(--header-height, 60px) + var(--space-sm, 0.5rem));
    z-index: 10;
    margin-bottom: var(--space-lg, 1.5rem);
  }

  /* Default to collapsed on mobile */
  .toc-container:not(.toc-open) {
    padding: var(--space-sm, 0.5rem) var(--space-md, 1rem);
  }
}

@media (max-width: $breakpoint-sm) {
  .toc-content a {
    font-size: var(--font-size-xs, 0.75rem);
    padding: 6px var(--space-xs, 0.25rem);
  }
}

/* Post Content */
.post-content {
  font-size: var(--font-size-base);
  line-height: var(--line-height-relaxed);
  color: var(--text-primary);
  margin-bottom: var(--space-2xl);

  h1, h2, h3, h4, h5, h6 {
    font-weight: var(--font-weight-bold);
    line-height: var(--line-height-tight);
    color: var(--text-primary);
    margin-top: var(--space-2xl);
    margin-bottom: var(--space-md);

    &:first-child {
      margin-top: 0;
    }
  }

  h2 {
    font-size: var(--font-size-2xl);
    padding-bottom: var(--space-sm);
    border-bottom: 1px solid var(--border);
  }

  h3 {
    font-size: var(--font-size-xl);
  }

  h4 {
    font-size: var(--font-size-lg);
  }

  p {
    margin: var(--space-md) 0;
  }

  a {
    color: var(--accent);
    text-decoration: none;
    border-bottom: 1px solid transparent;
    transition: border-color var(--transition-fast);

    &:hover {
      border-bottom-color: var(--accent);
    }
  }

  ul, ol {
    margin: var(--space-md) 0;
    padding-left: var(--space-xl);
  }

  li {
    margin: var(--space-xs) 0;
  }

  blockquote {
    margin: var(--space-lg) 0;
    padding: var(--space-md) var(--space-lg);
    border-left: 4px solid var(--accent);
    background-color: var(--bg-secondary);
    color: var(--text-secondary);

    p {
      margin: var(--space-sm) 0;
    }
  }

  code {
    font-family: var(--font-mono);
    font-size: 0.9em;
    padding: 2px 6px;
    background-color: var(--bg-secondary);
    border-radius: var(--radius-sm);
    color: var(--text-primary);
  }

  pre {
    margin: var(--space-lg) 0;
    padding: var(--space-lg);
    background-color: var(--bg-secondary);
    border-radius: var(--radius-md);
    overflow-x: auto;
    position: relative;

    code {
      padding: 0;
      background: none;
      font-size: 0.875rem;
    }
  }

  table {
    width: 100%;
    margin: var(--space-lg) 0;
    border-collapse: collapse;
    overflow-x: auto;
    display: block;
  }

  th, td {
    padding: var(--space-sm) var(--space-md);
    border: 1px solid var(--border);
    text-align: left;
  }

  th {
    background-color: var(--bg-secondary);
    font-weight: var(--font-weight-semibold);
  }

  img {
    max-width: 100%;
    height: auto;
    border-radius: var(--radius-md);
    margin: var(--space-lg) 0;
  }

  hr {
    border: none;
    border-top: 1px solid var(--border);
    margin: var(--space-2xl) 0;
  }
}

/* Post Footer */
.post-footer {
  border-top: 1px solid var(--border);
  padding-top: var(--space-xl);
  margin-top: var(--space-2xl);
}

/* Post Tags */
.post-tags {
  display: flex;
  align-items: center;
  gap: var(--space-sm);
  margin-bottom: var(--space-lg);
  flex-wrap: wrap;

  .icon {
    width: 18px;
    height: 18px;
    color: var(--text-secondary);
  }

  .tags-label {
    color: var(--text-secondary);
    font-weight: var(--font-weight-medium);
  }

  .tags-list {
    display: flex;
    flex-wrap: wrap;
    gap: var(--space-sm);
    list-style: none;
    padding: 0;
    margin: 0;
  }

  .tag-link {
    padding: 4px 12px;
    background-color: var(--bg-secondary);
    color: var(--text-primary);
    text-decoration: none;
    border-radius: var(--radius-full);
    font-size: var(--font-size-sm);
    transition: all var(--transition-fast);

    &:hover {
      background-color: var(--accent);
      color: #ffffff;
    }
  }
}

/* Post Navigation */
.post-nav {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--space-md);
  margin-top: var(--space-lg);
  max-width: 100%;
  box-sizing: border-box;
}

.post-nav-item {
  display: flex;
  align-items: center;
  gap: var(--space-sm);
  padding: var(--space-md);
  background-color: var(--bg-secondary);
  border-radius: var(--radius-md);
  text-decoration: none;
  color: var(--text-primary);
  transition: all var(--transition-fast);
  border: 1px solid var(--border);
  min-width: 0;
  max-width: 100%;
  box-sizing: border-box;

  &:hover {
    border-color: var(--accent);
    background-color: var(--bg-primary);
  }

  .icon {
    width: 20px;
    height: 20px;
    flex-shrink: 0;
    color: var(--text-secondary);
  }
}

.post-nav-prev {
  justify-content: flex-start;
}

.post-nav-next {
  justify-content: flex-end;
  text-align: right;
  grid-column: 2;
}

.post-nav-content {
  display: flex;
  flex-direction: column;
  gap: 4px;
  min-width: 0;
}

.post-nav-label {
  font-size: var(--font-size-sm);
  color: var(--text-secondary);
  font-weight: var(--font-weight-medium);
}

.post-nav-title {
  font-weight: var(--font-weight-semibold);
  color: var(--text-primary);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* Comments */
.comments-wrapper {
  max-width: var(--content-width);
  margin: var(--space-2xl) auto 0;
  padding: 0 var(--space-lg);
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .post-single {
    padding: var(--space-lg) var(--space-md);
  }

  .post-title {
    font-size: var(--font-size-2xl);
  }

  .post-nav {
    grid-template-columns: 1fr;
  }

  .post-nav-next {
    grid-column: 1;
  }

  .toc-container {
    position: sticky;
    top: calc(var(--header-height) + var(--space-md));
    z-index: 10;
  }
}
