/* Share Buttons */
.share-buttons {
  display: flex;
  align-items: center;
  gap: var(--space-md);
  flex-wrap: wrap;
  padding: var(--space-lg) 0;
  border-top: 1px solid var(--border);
  margin-top: var(--space-lg);
}

.share-label {
  display: flex;
  align-items: center;
  gap: var(--space-xs);
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  color: var(--text-secondary);

  .icon {
    width: 18px;
    height: 18px;
  }
}

.share-buttons-list {
  display: flex;
  align-items: center;
  gap: var(--space-sm);
  flex-wrap: wrap;
}

.share-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  border-radius: var(--radius-full);
  border: 1px solid var(--border);
  background-color: var(--bg-secondary);
  color: var(--text-secondary);
  text-decoration: none;
  transition: all var(--transition-fast);
  cursor: pointer;

  svg {
    width: 18px;
    height: 18px;
  }

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }

  &:active {
    transform: translateY(0);
  }
}

/* Platform-specific colors */
.share-twitter:hover {
  background-color: #1da1f2;
  border-color: #1da1f2;
  color: #ffffff;
}

.share-facebook:hover {
  background-color: #1877f2;
  border-color: #1877f2;
  color: #ffffff;
}

.share-linkedin:hover {
  background-color: #0a66c2;
  border-color: #0a66c2;
  color: #ffffff;
}

.share-reddit:hover {
  background-color: #ff4500;
  border-color: #ff4500;
  color: #ffffff;
}

.share-whatsapp:hover {
  background-color: #25d366;
  border-color: #25d366;
  color: #ffffff;
}

.share-telegram:hover {
  background-color: #0088cc;
  border-color: #0088cc;
  color: #ffffff;
}

.share-email:hover {
  background-color: var(--accent);
  border-color: var(--accent);
  color: #ffffff;
}

.share-copy {
  position: relative;

  &:hover {
    background-color: var(--accent);
    border-color: var(--accent);
    color: #ffffff;
  }

  &.copied {
    background-color: #10b981;
    border-color: #10b981;
    color: #ffffff;

    .copy-icon {
      display: none;
    }

    .check-icon {
      display: block !important;
    }
  }
}

/* Mobile Responsive */
@media (max-width: 640px) {
  .share-buttons {
    flex-direction: column;
    align-items: flex-start;
  }

  .share-buttons-list {
    width: 100%;
    justify-content: flex-start;
  }

  .share-button {
    width: 40px;
    height: 40px;

    svg {
      width: 20px;
      height: 20px;
    }
  }
}
