// Main entry point for SCSS
// Import all partials in the correct order

// 1. Variables and utilities
@import 'variables';

// 2. Reset and base styles
@import 'reset';

// 3. Typography
@import 'typography';

// 4. Layout
@import 'layout';

// 5. Components
@import 'header';
@import 'footer';
@import 'buttons';
@import 'search';
@import 'share';
@import 'comments';
@import 'profile';
@import 'related-posts';
@import 'reading-progress';
@import 'series';

// 6. Pages
@import 'post';
@import 'archive';
@import 'tags-categories-index';

// 7. Prevent flash of unstyled content
// Smooth transition when theme loads
html {
  transition: background-color var(--transition-base), color var(--transition-base);
}

// Smooth scrolling
html.smooth-scroll {
  scroll-behavior: smooth;
}
