// Header styles

.header {
  position: sticky;
  top: 0;
  z-index: var(--z-sticky);
  background-color: var(--bg-primary);
  border-bottom: 1px solid var(--border);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  transition: box-shadow var(--transition-base);

  &.scrolled {
    box-shadow: 0 2px 8px var(--shadow);
  }
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: var(--wide-width);
  margin: 0 auto;
  padding: 0 var(--space-md);
  height: var(--header-height);
}

// Logo
.logo {
  display: flex;
  align-items: center;
  gap: var(--space-sm);

  a {
    display: flex;
    align-items: center;
    gap: var(--space-sm);
    text-decoration: none;
    color: var(--text-primary);
    font-weight: var(--font-weight-semibold);
    font-size: var(--font-size-lg);

    &:hover {
      color: var(--accent);
      text-decoration: none;
    }

    &:focus-visible {
      outline: 2px solid var(--accent);
      outline-offset: 2px;
      border-radius: var(--radius-sm);
    }
  }

  .logo-image {
    width: 32px;
    height: 32px;
    border-radius: var(--radius-full);
  }

  .logo-text {
    white-space: nowrap;

    @media (max-width: $breakpoint-sm) {
      display: none;
    }
  }
}

// Menu
.menu {
  display: flex;
  align-items: center;
  gap: var(--space-lg);
  list-style: none;
  margin: 0;
  padding: 0;

  li {
    margin: 0;
  }

  a {
    display: inline-block;
    padding: var(--space-sm) var(--space-md);
    color: var(--text-secondary);
    text-decoration: none;
    font-size: var(--font-size-base);
    font-weight: var(--font-weight-medium);
    border-radius: var(--radius-sm);
    transition: all var(--transition-fast);

    &:hover {
      color: var(--text-primary);
      background-color: var(--bg-secondary);
      text-decoration: none;
    }

    &.active {
      color: var(--accent);
      background-color: var(--bg-secondary);
    }

    &:focus-visible {
      outline: 2px solid var(--accent);
      outline-offset: 2px;
    }
  }

  @media (max-width: $breakpoint-md) {
    display: none;
    position: absolute;
    top: var(--header-height);
    left: 0;
    right: 0;
    flex-direction: column;
    align-items: stretch;
    gap: 0;
    background-color: var(--bg-primary);
    border-bottom: 1px solid var(--border);
    padding: var(--space-md);
    box-shadow: 0 4px 12px var(--shadow);

    &.open {
      display: flex;
    }

    li {
      width: 100%;
    }

    a {
      display: block;
      width: 100%;
      padding: var(--space-md);
      text-align: left;
    }
  }
}

// Nav actions (search, theme toggle, mobile menu)
.nav-actions {
  display: flex;
  align-items: center;
  gap: var(--space-sm);
}

.search-button,
.theme-toggle,
.menu-toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  padding: 0;
  border: none;
  background-color: transparent;
  color: var(--text-secondary);
  border-radius: var(--radius-sm);
  cursor: pointer;
  transition: all var(--transition-fast);

  &:hover {
    color: var(--text-primary);
    background-color: var(--bg-secondary);
  }

  &:focus-visible {
    outline: 2px solid var(--accent);
    outline-offset: 2px;
  }

  svg {
    width: 20px;
    height: 20px;
  }
}

.theme-toggle {
  .sun {
    display: none;
  }

  .moon {
    display: block;
  }

  [data-theme="dark"] & {
    .sun {
      display: block;
    }

    .moon {
      display: none;
    }
  }
}

.menu-toggle {
  display: none;

  @media (max-width: $breakpoint-md) {
    display: flex;
  }
}

@media (min-width: ($breakpoint-md + 1px)) {
  .menu-toggle {
    display: none !important;
  }
}
